<template>
  <div class="w-full h-screen flex justify-center items-center">
    <div v-if="loading">
      <BaseLoading />
    </div>
  </div>
</template>

<script>
import { defineComponent, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import { usePrint } from 'composable/whitelabel'

import BaseLoading from 'components/BaseLoading.vue'

export default defineComponent({
  name: 'ShippingQR',
  components: {
    BaseLoading
  },
  setup() {
    const route = useRoute()
    const { printQR, loading } = usePrint()

    onMounted(async () => {
      const queries = Object.keys(route.query)
      const checked = queries.map(item => !!route.query[item]).filter(item => item)
      if ((queries.length !== checked.length) || !queries.length) {
        return window.open(location.origin, '_self')
      }

      return printQR(route.query)
    })

    return {
      loading
    }
  }
})
</script>
